import { useState } from "react";
import Editor from "react-simple-code-editor";
import "./prism.css";
import * as Prism from "prismjs";
import HTMLReactParser from "html-react-parser";

const HtmlParserContent = (props: any) => {
  const { mode } = props;

  const [code, setCode] = useState<string>("");
  const [scriptCode, setScriptCode] = useState<string>("");
  const [showScript, setShowScript] = useState<boolean>(false);
  const [isSaved, setIsSaved] = useState<boolean>(false);

  const changeMode = () => {
    setShowScript(!showScript);
  };

  const evalJS = () => {
    try {
      const timeout = setTimeout(() => {
        window.eval(code);
        clearTimeout(timeout);
      }, 0);
    } catch (err: any) {
      console.log(err);
    }
  };

  const saveData = () => {
    setIsSaved(true);
    const timeout = setTimeout(() => {
      setIsSaved(false);
      clearTimeout(timeout);
    }, 2000);
  };
  return (
    <>
      {mode == "view" ? (
        <>
          <div>
            {/* <iframe
              srcDoc={srcDoc}
              title="output"
              sandbox="allow-scripts"
              frameBorder="0"
              width="100%"
              height="400px"
            /> */}
            {HTMLReactParser(scriptCode, {
              replace: (domNode: any) => {
                if (domNode.name === "use") {
                  const src = domNode.attribs["xlink:href"];
                  const splitSrc = src.split("#");
                  const path = splitSrc[0]
                    ? splitSrc[0][0] === "/"
                      ? splitSrc[0].slice(1)
                      : splitSrc[0]
                    : "";
                  const hashtag = splitSrc[1] || "";
                  const newHref = `${path}?no_redirect=1#${hashtag}`;
                  return <use xlinkHref={newHref} />;
                }
              },
            })}
          </div>
          {evalJS()}
        </>
      ) : (
        <>
          <div className="editor-wrapper">
            {!showScript ? (
              <>
                <Editor
                  textareaId="code-editor"
                  value={code}
                  onValueChange={(code) => setCode(code)}
                  highlight={(code) =>
                    Prism.highlight(code, Prism.languages.html, "html")
                  }
                  padding={10}
                  style={{ fontSize: "12px" }}
                />
              </>
            ) : (
              <>
                <Editor
                  textareaId="script-editor"
                  value={scriptCode}
                  onValueChange={(scriptCode) => setScriptCode(scriptCode)}
                  highlight={(scriptCode) =>
                    Prism.highlight(
                      scriptCode,
                      Prism.languages.javascript,
                      "javascript"
                    )
                  }
                  padding={10}
                  style={{ fontSize: "12px" }}
                />
              </>
            )}
          </div>
          <div className="config-mode__footer">
            {/* <button className="save-btn" onClick={saveData}>
              Save
            </button> */}
            <div className="text-mode" onClick={changeMode}>
              {showScript ? "HTML/CSS Mode" : "Javascript Mode"}
            </div>
          </div>
          {isSaved && <div className="save-text">Saved</div>}
        </>
      )}
    </>
  );
};
export default HtmlParserContent;
