import React,{useState} from 'react';
import logo from './logo.svg';
import './App.css';
import HtmlParserContent from './HtmlParserContent';

function App() {
  const [mode,setMode]=useState('view')
  return (
    <div className='root'>
      <button onClick={()=>{
        if(mode==='view'){
          setMode('config')
        } else setMode('view')
      }}>{mode}</button>
      <HtmlParserContent mode={mode}></HtmlParserContent>
    </div>
  );
}

export default App;
